@use 'hero';
@use 'componentes/infos_bar';
@use 'variables';
@use 'event';

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Roboto, sans-serif;
    color: #fff;
    list-style: none;
}

.container {
    max-width: 960px;
    width: 100%;
    margin: 0 auto;

    @media(max-width:640px){
        max-width: 80%;
    }

    @media(max-width:1024){
        max-width: 90%;
    }
}